import Image from "next/image";
import { useState } from "react";
import { CheckSquare } from "lucide-react";

const steps = [
  "Daftar Akun",
  "Pilih Gelombang",
  "Isi Identitas Diri",
  "Unggah Berkas",
  "Pilih Jurusan",
  "Bayar Pendaftaran",
  "Cetak Kartu Peserta",
];

// Detail langkah sesuai urutan di situs SMK Telkom Malang
const stepDetails: Record<number, string[]> = {
  0: [
    'Klik tombol "Daftar Sekarang" di atas',
    "Pilih tab Daftar",
    "Masukkan data awal pendaftaran",
    "Login menggunakan email dan password ketika daftar",
  ],
  1: ["Pilih gelombang pendaftaran yang tersedia sesuai jadwal."],
  2: ["Isi identitas diri lengkap sesuai data diri calon peserta didik."],
  3: ["Unggah berkas persyaratan pendaftaran sesuai petunjuk."],
  4: ["Pilih jurusan sesuai minat dan bakat Anda."],
  5: ["Lakukan pembayaran biaya pendaftaran melalui sistem yang disediakan."],
  6: ["Cetak kartu peserta dan simpan untuk keperluan tes masuk."],
};

export default function AlurPendaftaran() {
  const [activeStep, setActiveStep] = useState(0);

  return (
    <section id="alur-pendaftaran" className="bg-white py-20">
      <div className="items-center gap-10 grid md:grid-cols-2 mx-auto px-6 container">
        {/* Kiri: konten teks */}
        <div>
          <h2 className="mb-2 font-bold text-gray-900 text-4xl">
            Alur Pendaftaran
          </h2>
          <p className="mb-6 font-light text-gray-600 text-2xl">bagaimana?</p>
          <p className="mb-8 max-w-lg text-gray-600">
            Terdapat beberapa step pendaftaran yang harus dipenuhi agar semua
            rangkaian pendaftaran hingga seleksi berjalan dengan lancar.
          </p>

          {/* Langkah-langkah */}
          <div className="gap-3 grid grid-cols-3 sm:grid-cols-4 lg:grid-cols-7 mb-8">
            {steps.map((step, idx) => (
              <button
                key={idx}
                onClick={() => setActiveStep(idx)}
                className={`py-3 px-2 rounded-lg border text-sm font-medium transition-all duration-200 ${
                  activeStep === idx
                    ? "bg-red-600 text-white shadow-md"
                    : "bg-white text-gray-800 border-gray-200 hover:bg-gray-100"
                }`}
              >
                <div className="mb-1 font-semibold text-lg">{idx + 1}</div>
                {step}
              </button>
            ))}
          </div>

          {/* Detail langkah aktif */}
          <ul className="space-y-3">
            {stepDetails[activeStep]?.map((text, i) => (
              <li key={i} className="flex items-start space-x-3">
                <CheckSquare className="flex-shrink-0 mt-0.5 w-5 h-5 text-red-500" />
                <span className="text-gray-700">{text}</span>
              </li>
            ))}
          </ul>
        </div>

        {/* Kanan: ilustrasi */}
        <div className="flex justify-center">
          <Image
            src="/images/daftar-illustration.svg"
            alt="Ilustrasi pendaftaran siswa"
            width={400}
            height={400}
            className="drop-shadow-md"
          />
        </div>
      </div>
    </section>
  );
}
